<template>
  <div id="app" class="wrapper">
    <div class="card-form">
      <div class="card-list">
        <div class="card-item" :class="{ '-active' : isCardFlipped }">
          <div class="card-item__side -front">
            <div ref="focusElement" class="card-item__focus" :class="{'-active' : focusElementStyle }" :style="focusElementStyle"></div>
            <div class="card-item__cover">
              <img v-if="currentCardBackground" :src="currentCardBackground" class="card-item__bg">
            </div>

            <div class="card-item__wrapper">
              <div class="card-item__top">
                <img src="../assets/creditcard/chip.png" class="card-item__chip">
                <div class="card-item__type" :class="[getCardType]">
                </div>
              </div>
              <!-- 信用卡卡號 -->
              <label ref="cardNumber" for="cardNumber" class="card-item__number">
                <template v-if="getCardType === 'amex'">
                  <span v-for="(n, $index) in amexCardMask" :key="$index">
                    <transition name="slide-fade-up">
                      <div
                        v-if="$index > 4 && $index < 14 && card.number.length > $index && n.trim() !== ''"
                        class="card-item__numberItem"
                      >*</div>
                      <div
                        v-else-if="card.number.length > $index"
                        :key="$index"
                        class="card-item__numberItem"
                        :class="{ '-active' : n.trim() === '' }"
                      >
                        {{ card.number[$index] }}
                      </div>
                      <div
                        v-else
                        :key="$index + 1"
                        class="card-item__numberItem"
                        :class="{ '-active' : n.trim() === '' }"
                      >{{ n }}</div>
                    </transition>
                  </span>
                </template>
                <template v-else>
                  <span v-for="(n, $index) in getCardType === 'dinersclub14'? dinersclub14CardMask : otherCardMask" :key="$index">
                    <transition name="slide-fade-up">
                      <div
                        v-if="$index > 4 && $index < 15 && card.number.length > $index && n.trim() !== ''"
                        class="card-item__numberItem"
                      >*</div>
                      <div
                        v-else-if="card.number.length > $index"
                        :key="$index"
                        class="card-item__numberItem"
                        :class="{ '-active' : n.trim() === '' }"
                      >
                        {{ card.number[$index] }}
                      </div>
                      <div
                        v-else
                        :key="$index + 1"
                        class="card-item__numberItem"
                        :class="{ '-active' : n.trim() === '' }"
                      >{{ n }}</div>
                    </transition>
                  </span>
                </template>
              </label>
              <div class="card-item__content">
                <label v-if="cardHolder" ref="cardName" for="cardName" class="card-item__info">
                  <div class="card-item__holder">{{ Dict.card_holder }}</div><!-- 持卡人姓名 -->
                  <transition name="slide-fade-up">
                    <div v-if="card.name.length" key="1" class="card-item__name">
                      <transition-group name="slide-fade-right">
                        <template v-for="(n, $index) in card.name.replace(/\s\s+/g, ' ')">
                          <span
                            v-if="$index === $index"
                            :key="$index + 1"
                            class="card-item__nameItem"
                          >{{ n }}</span>
                        </template>
                      </transition-group>
                    </div>
                    <div v-else key="2" class="card-item__name">＊＊＊＊＊</div>
                  </transition>
                </label>
                <div ref="cardDate" class="card-item__date">
                  <label for="cardMonth" class="card-item__dateTitle">{{ Dict.expires }}</label><!-- 期限 -->
                  <label for="cardMonth" class="card-item__dateItem">
                    <transition name="slide-fade-up">
                      <span v-if="card.month" :key="card.month">{{ card.month }}</span>
                      <span v-else key="2">MM</span>
                    </transition>
                  </label>
                  /
                  <label for="cardYear" class="card-item__dateItem">
                    <transition name="slide-fade-up">
                      <span v-if="card.year" :key="card.year">{{ String(card.year).slice(2,4) }}</span>
                      <span v-else key="2">YY</span>
                    </transition>
                  </label>
                </div>
              </div>
            </div>
          </div>
          <div class="card-item__side -back">
            <div class="card-item__cover">
              <img v-if="currentCardBackground" :src="currentCardBackground" class="card-item__bg">
            </div>
            <div class="card-item__band"></div>
            <div class="card-item__cvv">
              <div class="card-item__cvvTitle">
                {{ Dict.cvv }}
              </div><!-- 檢查碼 (CVV) -->
              <div class="card-item__cvvBand">
                <span v-for="(n, $index) in card.cvv" :key="$index">*</span>
              </div>
              <div class="card-item__type" :class="[getCardType]">
              </div>
            </div>
          </div>
        </div>
      </div>
      <div>
        <slot name="note"></slot>
      </div>
      <div class="card-form__inner">
        <div class="card-input">
          <label for="cardNumber" class="card-input__label">{{ Dict.card_number }}</label><!-- 信用卡卡號 -->
          <input
            id="cardNumber"
            v-model="card.number"
            v-mask="generateCardNumberMask"
            type="text"
            class="card-input__input"
            data-ref="cardNumber"
            autocomplete="off"
            @focus="focusInput"
            @blur="blurInput"
          >
        </div>
        <div v-if="cardHolder" class="card-input">
          <label for="cardName" class="card-input__label">{{ Dict.card_holder }}</label><!-- 持卡人姓名 -->
          <input id="cardName" v-model="card.name" type="text" class="card-input__input" data-ref="cardName" autocomplete="off" @focus="focusInput" @blur="blurInput">
        </div>
        <div class="card-form__row">
          <div class="card-form__col">
            <div class="card-form__group">
              <label for="cardMonth" class="card-input__label">{{ Dict.expires }}</label><!-- 期限 -->
              <select id="cardMonth" v-model="card.month" class="card-input__input -select" data-ref="cardDate" @focus="focusInput" @blur="blurInput">
                <option value="" disabled selected>
                  {{ Dict.month }}
                </option><!-- 月 -->
                <option v-for="n in 12" :key="n" :value="parseOptionItem(n)" :disabled="n < minCardMonth">
                  {{ parseOptionItem(n) }}
                </option>
              </select>
              <select id="cardYear" v-model="card.year" class="card-input__input -select" data-ref="cardDate" @focus="focusInput" @blur="blurInput">
                <option value="" disabled selected>
                  {{ Dict.year }}
                </option><!-- 西元年 -->
                <option v-for="(n, $index) in 12" :key="n" :value="$index + minCardYear">
                  {{ $index + minCardYear }}
                </option>
              </select>
            </div>
          </div>
          <div class="card-form__col -cvv">
            <div class="card-input">
              <label for="cardCvv" class="card-input__label">{{ Dict.cvv }}</label><!-- 檢查碼 (CVV) -->
              <input
                id="cardCvv"
                v-model="card.cvv"
                v-mask="getCardType==='amex'?'####':'###'"
                type="text"
                class="card-input__input"
                :maxlength="getCardType==='amex'?4:3"
                autocomplete="off"
                @focus="flipCard(true)"
                @blur="flipCard(false)"
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// https://github.com/muhammederdem/credit-card-form
// https://codepen.io/JavaScriptJunkie/pen/YzzNGeR
export default {
  name: 'Creditcard',
  directives: {
    mask: {
      inserted(el, binding, vnode, oldVnode) {
        // 被綁定元素插入父節點時調用(僅保證父節點存在，但不一定已被插入文檔中)
        // window.console.log('inserted')

        // 目標需為 input|textarea
        let target = el
        if (!['INPUT', 'TEXTAREA'].includes(target.nodeName)) {
          target = el.querySelector('input') || el.querySelector('textarea')
        }
        if (!target) return

        // placeholder 預設使用 mask
        // if (target.placeholder === '') {
        //   target.placeholder = binding.value
        // }

        target.addEventListener('input', (e) => {
          const result = replaceMask({
            type: e.inputType,
            input: e.target.value,
            mask: binding.value
          })
          e.target.value = result
          // window.console.log(result)

          // update props
          const dataResult = [...vnode.context.value]
          vnode.context.dataResult = dataResult
        })
        target.addEventListener('blur', (e) => {
          // window.console.log('inserted-blur', e)
          e.target.value = e.target.value.trim()
          const result = replaceMask({
            type: e.inputType,
            input: e.target.value,
            mask: binding.value
          })
          e.target.value = result
          delete window.backup_maskInput

          // update props
          const dataResult = [...vnode.context.value]
          vnode.context.dataResult = dataResult
        })

        /**
        * # Number (0-9)
        * A Letter in any case (a-z,A-Z)
        * N Number or letter (a-z,A-Z,0-9)
        */
        function replaceMask(options) {
          let result = []
          const charList = ['#', 'A', 'N']
          const { type, input, mask } = options
          // e.inputType === "deleteContentBackward" // Backspace
          // e.inputType === "deleteContentForward"  // Delete
          // e.inputType === "insertText"            // When insert character
          if (['deleteContentBackward', 'deleteContentForward'].includes(type) || !mask) {
            window.backup_maskInput = input.split('')
            return input
          }

          const regExp = mask.match(/^\/([\S\s]*)\/$/)
          const isRegExp = regExp && new RegExp(mask) instanceof RegExp
          if (isRegExp) {
            const pattern = mask.slice(1, -1)
            const checkRegExp = `${input}`.match(new RegExp(pattern, 'g'))
            if (checkRegExp) {
              result = checkRegExp
              window.backup_maskInput = checkRegExp
            }
            else {
              result = window.backup_maskInput || []
            }
            // window.console.log('replaceMask reg', {
            //   input,
            //   result
            // })
          }
          else {
            // 移除非數字英文
            const inputValue = `${input}`.replaceAll(/\W/g, '')
            const maskArr = mask.split('')
            const maxLength = mask.replaceAll(/[^#|^A|^N]/g, '').length

            // 輸入長度需小於等於 mask 扣掉符號
            let inputResult = ''
            if (inputValue.length <= maxLength) {
              inputResult = inputValue
            }
            else {
              inputResult = inputValue.substring(0, maxLength)
            }

            // 用 inputResult 逐字取代 mask
            let dx = 0
            inputResult.split('').forEach((v, i) => {
            // 若 mask 為 #|A|N
              const maskValue = maskArr[i + dx]
              // console.log('mask', maskValue)
              if (charList.includes(maskValue)) {
                result.push(v)
              }
              else {
                dx += 1
                result.push(maskValue)
                result.push(v)
              }
            })

            // console.log('replaceMask', {
            //   inputValue,
            //   result
            // })
          }
          return result.join('')
        }
      }
    }
  },
  props: {
    lang: {
      type: String,
      default: 'tw'
    },
    dict: {
      type: Object,
      default() {
        return {
          'tw': {
            'card_holder': '持卡人姓名',
            'expires': '期限',
            'card_number': '信用卡卡號',
            'cvv': '檢查碼 (CVV)',
            'year': '西元年',
            'month': '月'
          },
          'en': {
            'card_holder': 'Card Holder',
            'expires': 'Expiration Date',
            'card_number': 'Card Number',
            'cvv': 'CVV',
            'year': 'Year',
            'month': 'Month'
          }
        }
      }
    },
    cardHolder: {
      type: Boolean,
      default() {
        return true
      }
    },
    card: {
      type: Object,
      default() {
        return {
          name: '',
          number: '',
          month: '',
          year: '',
          cvv: '',
          type: ''
        }
      }
    }
  },
  data() {
    return {
      currentCardBackground: '',
      // `https://raw.githubusercontent.com/muhammederdem/credit-card-form/master/src/assets/images/${Math.floor(Math.random() * 25 + 1)}.jpeg`, // just for fun :D
      minCardYear: new Date().getFullYear(),
      amexCardMask: '#### ###### #####',
      dinersclub14CardMask: '#### #### #### ##',
      otherCardMask: '#### #### #### ####',
      cardNumberTemp: '',
      isCardFlipped: false,
      focusElementStyle: null,
      isInputFocused: false
    }
  },
  computed: {
    Dict() {
      return this.dict[this.lang]
    },
    getCardType() {
      const number = this.card.number.replace(/\s/g, '')
      // https://stackoverflow.com/questions/9315647/regex-credit-card-number-tests
      // Amex Card: ^3[47][0-9]{13}$
      // BCGlobal: ^(6541|6556)[0-9]{12}$
      // Carte Blanche Card: ^389[0-9]{11}$
      // Diners Club Card: ^3(?:0[0-5]|[68][0-9])[0-9]{11}$
      // Discover Card: ^65[4-9][0-9]{13}|64[4-9][0-9]{13}|6011[0-9]{12}|(622(?:12[6-9]|1[3-9][0-9]|[2-8][0-9][0-9]|9[01][0-9]|92[0-5])[0-9]{10})$
      // Insta Payment Card: ^63[7-9][0-9]{13}$
      // JCB Card: ^(?:2131|1800|35\d{3})\d{11}$
      // KoreanLocalCard: ^9[0-9]{15}$
      // Laser Card: ^(6304|6706|6709|6771)[0-9]{12,15}$
      // Maestro Card: ^(5018|5020|5038|6304|6759|6761|6763)[0-9]{8,15}$
      // Mastercard: ^(5[1-5][0-9]{14}|2(22[1-9][0-9]{12}|2[3-9][0-9]{13}|[3-6][0-9]{14}|7[0-1][0-9]{13}|720[0-9]{12}))$
      // Solo Card: ^(6334|6767)[0-9]{12}|(6334|6767)[0-9]{14}|(6334|6767)[0-9]{15}$
      // Switch Card: ^(4903|4905|4911|4936|6333|6759)[0-9]{12}|(4903|4905|4911|4936|6333|6759)[0-9]{14}|(4903|4905|4911|4936|6333|6759)[0-9]{15}|564182[0-9]{10}|564182[0-9]{12}|564182[0-9]{13}|633110[0-9]{10}|633110[0-9]{12}|633110[0-9]{13}$
      // Union Pay Card: ^(62[0-9]{14,17})$
      // Visa Card: ^4[0-9]{12}(?:[0-9]{3})?$
      // Visa Master Card: ^(?:4[0-9]{12}(?:[0-9]{3})?|5[1-5][0-9]{14})$

      const cards = [
        {
          name: 'Amex Card',
          key: 'amex',
          reg: '^3[47][0-9]{0,13}'
        },
        {
          name: 'Diners Club Card',
          key: 'dinersclub',
          reg: '^3(?:0[0-5]|[68][0-9])[0-9]{12}'
        },
        {
          name: 'Diners Club Card (14 digit card)',
          key: 'dinersclub14',
          reg: '^3(?:0[0-5]|[68][0-9])[0-9]{0,11}'
        },
        {
          name: 'JCB Card',
          key: 'jcb',
          reg: '^(?:2131|1800|35\\d{3})\\d{11}'
        },
        {
          name: 'Discover Card',
          key: 'discover',
          reg: '^65[4-9][0-9]{13}|64[4-9][0-9]{13}|6011[0-9]{12}|(622(?:12[6-9]|1[3-9][0-9]|[2-8][0-9][0-9]|9[01][0-9]|92[0-5])[0-9]{10})'
        },
        {
          name: 'Mastercard',
          key: 'mastercard',
          reg: '^(5[1-5][0-9]{14}|2(22[1-9][0-9]{12}|2[3-9][0-9]{13}|[3-6][0-9]{14}|7[0-1][0-9]{13}|720[0-9]{12}))'
        },
        {
          name: 'Union Pay',
          key: 'unionpay',
          reg: '^(62[0-9]{14,17})'
        },
        {
          name: 'Visa Card',
          key: 'visa',
          reg: '^4[0-9]{12}(?:[0-9]{3})'
        }
        // {
        //   name: 'troy',
        //   key: 'troy',
        //   reg: '^9792'
        // },
      ]

      let type = ''
      for (const k in cards) {
        const v = cards[k]
        const reg = new RegExp(v.reg)
        if (number.match(reg) != null) {
          type = v.key
          break
        }
      }
      return type // default type
    },
    generateCardNumberMask() {
      switch (this.getCardType) {
        case 'amex':
          return this.amexCardMask
        case 'dinersclub14':
          return this.dinersclub14CardMask
        default:
          return this.otherCardMask
      }
    },
    minCardMonth() {
      if (this.card.year === this.minCardYear) return new Date().getMonth() + 1
      return 1
    }
  },
  watch: {
    'card.year': function() {
      if (this.card.month < this.minCardMonth) {
        this.card.month = ''
      }
    },
    'card.number': function() {
      this.card.type = this.getCardType
    }
  },
  mounted() {
    this.cardNumberTemp = this.otherCardMask
    document.getElementById('cardNumber').focus()
  },
  methods: {
    parseOptionItem(n) {
      return n < 10 ? '0' + n : n
    },
    flipCard(status) {
      this.isCardFlipped = status
    },
    focusInput(e) {
      this.isInputFocused = true
      const targetRef = e.target.dataset.ref
      const target = this.$refs[targetRef]
      this.focusElementStyle = {
        width: `${target.offsetWidth}px`,
        height: `${target.offsetHeight}px`,
        transform: `translateX(${target.offsetLeft}px) translateY(${target.offsetTop}px)`
      }
    },
    blurInput() {
      const vm = this
      setTimeout(() => {
        if (!vm.isInputFocused) {
          vm.focusElementStyle = null
        }
      }, 300)
      vm.isInputFocused = false
    }
  }
}
</script>

<style lang="scss" scoped>
